import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import * as React from "react";
import Figure from "react-bootstrap/Figure";
import Badge from "react-bootstrap/Badge";
import compact from "lodash/compact";

interface IMenuItemProps {
    icon: string;
    title: string;
    description: string;
    disabled?: boolean;
    badge?: string | number;
    onClick(): void;
    onClickDisabled?(): void;
}

export default class MenuItemComponent extends React.Component<IMenuItemProps> {
    render() {
        const {icon, badge, title, description, disabled, onClick, onClickDisabled} = this.props;
        return <Row className={compact(["menu-item", disabled && "disabled"]).join(" ")} onClick={disabled ? onClickDisabled : onClick}>
            <Col xs={2} className="left">
                <Figure>
                    <Figure.Image src={icon}/>
                </Figure>
            </Col>
            <Col className={"right flex-grow"}>
                <Row>
                    <Col className={"title flex-grow"}>
                        {title}
                    </Col>
                    <Col xs={"auto"}>
                        {badge && <Badge variant={"success"} pill={true}>{badge}</Badge>}
                    </Col>
                </Row>
                <Row className={"description"}>
                    <Col className={"flex-fill"}>
                        <span>
                            {description}
                        </span>
                    </Col>
                </Row>
            </Col>
        </Row>;
    }
}
