import Modal from "react-bootstrap/Modal";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Figure from "react-bootstrap/Figure";
import QRCode from "qrcode.react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import React from "react";
import "InviteComponent.css";
import {phone} from "./Globals";
import {DEFAULT_PLAYER_NAME} from "./App";

interface IInviteComponentProps {
    gameUrl: string;
    playerName: string;

    changePlayerName(value: string): void;

    play(): void;

    dismiss(): void;
}

export default class InviteComponent extends React.Component<IInviteComponentProps> {
    public render() {
        const {gameUrl, playerName, changePlayerName, play, dismiss} = this.props;
        return <Modal className={"invite"} backdrop={"static"} show={true} onHide={dismiss} scrollable={false}>
            {
                phone()
                    ? <div className={"top-spacer"}/>
                    : <Modal.Header closeButton={true}>
                        <Modal.Title>
                            Invite a Friend!
                        </Modal.Title>
                    </Modal.Header>
            }
            <Modal.Body>
                <p>
                    {phone() ? "Have a friend" : "Have them"} open their camera and point at this QR code.
                    <br/>
                    They'll join you automatically!
                </p>
                <Row>
                    <Col className="flex-grow"/>
                    <Col>
                        <Figure>
                            <div className={"qr"}>
                                <QRCode size={600} value={gameUrl}/>
                            </div>
                            {!phone() && <Figure.Caption>
                                You can also copy and send them this URL:<br/>
                                <a href={gameUrl} target="_blank" rel="noopener noreferrer">{gameUrl}</a>
                            </Figure.Caption>}
                        </Figure>
                    </Col>
                    <Col className="flex-grow"/>
                </Row>
                <Row>
                    {phone() && <Col className="flex-grow"/>}
                    <Col>
                        <Form.Group>
                            {!phone() && <Form.Label>Your Name</Form.Label>}
                            <Form.Control type="input" placeholder="Your Name"
                                          value={playerName === DEFAULT_PLAYER_NAME ? undefined : playerName}
                                          onChange={changeNameThunk}/>
                        </Form.Group>
                    </Col>
                    {phone() && <Col className="flex-grow"/>}
                </Row>
            </Modal.Body>
            {phone()
                ? <Row>
                    <Col className="flex-grow"/>
                    <Col xs={4}>
                        <Button variant="primary" onClick={play} style={{width: "100%"}}>Play</Button>
                    </Col>
                    <Col className="flex-grow"/>
                </Row>
                : <Modal.Footer>
                    <Button variant="primary" onClick={play}>Play</Button>
                </Modal.Footer>
            }
        </Modal>;

        function changeNameThunk(event: any) {
            changePlayerName(event.target.value)
        }
    }
}
